import ColorButton from './components/colorButton';
import ColorAndHighlightButton from './components/colorAndHighlightButton';
import { ColorToolbarConfig } from '../utils/types';

export default function toolbar(colorConfig: ColorToolbarConfig) {
  const colorLocaleConfig = colorConfig.locale;
  return {
    buttons: {
      default: ColorButton,
      colorAndHighlight: ColorAndHighlightButton,
    },
    locale: colorLocaleConfig,
  };
}
